import md5 from 'js-md5';
import  { inputCheck, checkPassword } from '@/common/js/inputCheck.js';
import {error} from '@/common/js/errorTips.js'
import { getPhoneCode, validataCode, userRegister } from '@/common/api/quickRegister.js';

export default {
	data() {
		return {
			// 表单的数据
			form: {
				name: '',
				phone: '',
				identifyCode: '',
				password: '',
				checkPassword: ''
			},
			isValidate: true, // 用作验证这些表单是否都通过验证
			getCodeNum: 0
		}
	},
	methods: {
		// 获取手机验证码
		getPhoneCode: function() {
			if(this.isValidate && this.form.phone !== '') {
				this.getCodeNum = 60;
				let _this = this
				let time = setInterval(function(){
					if(_this.getCodeNum == 0) {
						clearInterval(time);
						return;
					}
						_this.getCodeNum--;
					},1000)
				
				getPhoneCode(this.form.phone).then(res => {
				}).catch(() => {
					uni.showToast({
						title: '发送失败，请检查网络',
						icon: 'none'
					})
				})
			}
		},
		// 校验验证码
		validataCode: function() {
			// 校验表单验证是否都过关
			for(let item in this.form) {
				if(this.form[item] === '' || this.form[item] === null
				 || this.form[item] === undefined) {
					this.isValidate = false;
					 break;
				 }
			}
			if(this.isValidate) {
				// 表达验证没问题之后首先验证验证码是否正确
				uni.showLoading({
					title: '加载中'
				})
				validataCode(this.form.phone, this.form.identifyCode).then(res => {
					console.log(res)
					if(this.form.identifyCode === '123456') {
						this.toAddCard()
					} else {
						uni.hideLoading()
						error('验证码错误')
						
					}
				}).catch(() => {
					error('网络')
				})
			} else {
				error('请将信息填写完整')
			}
		},
		// 将数据存进数据库后跳转到个人中心页面
		toAddCard:function(){
			userRegister({

				avatarUrl: uni.getStorageSync('avatarUrl'),





				name: this.form.name,
				password: md5(this.form.password),
				phone: this.form.phone
			}).then(res => {
				if(res.data.code === 200) {
					uni.hideLoading()
					uni.showToast({
						title: '注册成功',
						icon: 'success'
					})
					uni.navigateBack()
				} else {
					uni.hideLoading()
					error('该手机号已存在，请登录',)
				}
			}).catch(() => {
				uni.hideLoading()
				error('网络')
			})
				
		},
		// 用于表单的判断
		inputCheck:function(name, rule, value){
			let _this = this
			var error = inputCheck(name, rule, this.form[value])
			if(error !== 'ok'){
				this.isValidate = false;
				this.form[value] = ''
				error(error)
			} else {
				return
			}
		},
		// 判断两次输入的密码
		checkPassword:function(){
			var error = checkPassword(this.form.password, this.form.checkPassword)
			if(error !== 'ok'){
				this.isValidate = false
				error(error)
				return 
			} else {
				this.isValidate = true
				return
			}
		}
	}
}
